@charset "utf-8";

body {
  font-size: 16px;
  color: #333;
  background-color: #E9E6DD;
}

h1 {
  margin-bottom: 20px;
  font-size: 36px;
}

h2 {
  font-size: 18px;
}

.wrap {
  position: relative;
  margin: 0 auto;
  padding: 80px 0 0 0;
  width: 720px;
}

.visible {
  display: block !important;
}

.ribbon {
  position: fixed;
  top: 0;
  right: 0
}


#result {
  margin-bottom: 20px;
  padding: 10px 20px;
  height: 200px;
  border: solid 1px rgba(0,0,0,0.5);
  border-radius: 6px;
  background-color: #fff;

  span {
    line-height: 1.5;
  }
}

#btn-mic {
  margin-right: 5px;

  span {
    display: inline-block;
    margin: 1px 0 0 5px;
    width: 10px;
    height: 10px;
    border: solid 1px #fff;
    background: #bbb;
    border-radius: 50%;
  }

  &.on span {
    background: green;
  }
} 

#icon-music {
  display: none;
  position: absolute;
  top: 100px;
  right: 20px;
  font-size: 60px;
  animation-name: shake;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
}

@keyframes shake {
  0% {
    transform: rotate(-5deg);
  }
  50% {
    transform: rotate(10deg);
  }
  100% {
    transform: rotate(-5deg);
  }
}

#commands {
  position: absolute;
  bottom: 77px;
  right: 1px;
}

#commands-inner {
  position: relative;
  span {
    position: absolute;
    top: 0;
    right: 0;
    display: inline-block;
    width: 30px;
    height: 20px;
    text-align: center;
    cursor: pointer;
    background-color: #0091A0;
    color: #fff;
    font-size: 20px;
    line-height: 1;
    border-radius: 3px 0px 5px 0px;
  }
   
  &:hover > div { 
    display: block;
  }

  div {
    display: none;
    position: absolute;
    top: 24px;
    right: -16px;
    width: 500px;
    background-color: #366473;
    padding: 10px 20px;
    border-radius: 3px;
    color: #fff;
    box-shadow: 2px 2px 5px 1px rgba(0,0,0,0.14);

    ul {
      line-height: 1.5;
    }
  }
}

button {
  padding: 0.4em 0.7em 0.5em 0.7em;
  min-width: 150px;
  background-color: #A08981;
  border: none;
  border-radius: 2px;
  color: white;
  font-family: 'Roboto', sans-serif;
  font-size: 1em;
  cursor: pointer;
}

button:active,
button:hover {
  background-color: #917971;
}